.header {
    box-sizing: border-box;
    width: 100%;
    background-color: lightcoral;
    display: flex;
    flex-direction: column;
    background: url(../../images/bordercollie.jpg);
    background-size: cover;
}

.headerAdopt {
    box-sizing: border-box;
    width: 100%;
    background-color: rgb(8, 29, 41);
    display: flex;
    justify-content: center;
}

