@media screen and (max-width: 820px) {
    .header {
        height: 100vh;
        display: flex;
        justify-content: flex-end;
        padding: 0 70px 45px 15px;
    }

    .header h2 {
        font-size: 42px;
        color: #ffffff;
    }

    .headerAdopt {
        height: 35vh;
        display: flex;
        justify-content: center;
        align-items: flex-end;
    }

    .headerAdopt h1 {
        font-size: 32px;
        margin-bottom: 50px;
        color: #dadada;
    }
}